import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import './App.css';
import WelcomePanel, { id as WelcomePanelId } from './WelcomePanel';
import WhoPanel, { id as WhoPanelId } from './WhoPanel';
import ProjectsPanel, { id as ProjectsPanelId } from './ProjectsPanel';
import WorksPanel, { id as WorksPanelId } from './WorksPanel';
import KnowledgePanel, { id as KnowledgePanelId } from './KnowledgePanel';
import FloatingNavPanel from './FloatingNavPanel';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { panelIndex: 0 };
  }

  getOffset(element){
    var bounding = element.getBoundingClientRect();
    return {
      top: bounding.top + document.body.scrollTop,
      left: bounding.left + document.body.scrollLeft
    };
  };

  handleScroll(){
    const windowHeight = window.innerHeight;
    const maxPanelIndex = 5;

    const offset = this.getOffset(ReactDOM.findDOMNode(this.whoPanel));
    const windowsScrollTop = window.pageYOffset;
    if (windowsScrollTop >= offset.top) {
      ReactDOM.findDOMNode(this.floatingNavPanel).classList.add("FloatingNavPanel");
    }else{
      ReactDOM.findDOMNode(this.floatingNavPanel).classList.remove("FloatingNavPanel");
    }
    let panelIndex = 0;
    if (windowsScrollTop > windowHeight/2) {
      panelIndex = Math.min(
        Math.floor((windowsScrollTop + windowHeight * 3/2) / windowHeight),
        maxPanelIndex
      );
    }
    this.setState({ panelIndex: panelIndex });
  };

  componentDidMount() {
      window.addEventListener('scroll', this.handleScroll.bind(this));
  };

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll.bind(this));
  };


  render() {
    const { panelIndex } = this.state;

    return (
      <div className="App">
        <FloatingNavPanel
          ref={(panel) => { this.floatingNavPanel = panel; }}
          navigationItems={
            [
              { name: 'Welcome', id: WelcomePanelId },
              { name: 'Who', id: WhoPanelId },
              { name: 'Projects', id: ProjectsPanelId },
              { name: 'Works', id: WorksPanelId },
              { name: 'Knowledge', id: KnowledgePanelId }
            ]
          }
          currentNavigationItemIndex={ panelIndex }
        />
        <WelcomePanel />
        <WhoPanel ref={(panel) => { this.whoPanel = panel; }} />
        <ProjectsPanel />
        <WorksPanel />
        <KnowledgePanel />
      </div>
    );
  }
}

export default App;



