import React, { Component } from 'react';
import './WelcomePanel.css';
import linkedInIcon from './res/In-2C-28px-R.png';
import twitterIcon from './res/Twitter_Social_Icon_Circle_Color.svg';
import githubIcon from './res/GithubIcon.png';
export const id = 'welcomePanel';

class WelcomePanel extends Component {
  componentDidMount() {

  }
  render() {
    return (
      <div className="WelcomePanel" id={id}>
        <div className="WelcomePanelBlurb">
          <div>
            Lyndsey Ferguson
            <p/>
            <a href="https://twitter.com/lyndseydf?ref_src=twsrc%5Etfw"
               class="twitter-follow-button" 
               style={{ color: '#FFFFFF' }} 
               data-show-count="false" >
              <img src={twitterIcon} width='28' />
            </a>
            <script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
            &nbsp; &nbsp;
            <a style={{textDecoration: 'none', align: 'right'}} 
               href="https://www.linkedin.com/in/lyndsey-ferguson-25001522/">
               <img src={linkedInIcon} />
            </a>
            &nbsp;
            <a style={{textDecoration: 'none'}}
              href="https://github.com/lyndsey-ferguson">
              <img src={githubIcon} width='28' />
            </a>
          </div>
        </div>
        <div class="arrow bounce">
          <a class="fa fa-arrow-down fa-2x" href="#WhoPanel"></a>
        </div>
      </div>
    );
  }
}

export default WelcomePanel;



