module.exports = "<!-- metadata\ntitle: Crossplatform network software license validator\ntags: C++, Win32 network APIs, Win32 multithreading APIs, POSIX network APIs, POSIX multithreading APIs\n-->\n\n#### Improved the functionality that asserts that a software license is being used only once on a local network.\n2002\n\n##### Why did I enjoy this project?\nI enjoyed this project because I improved the performance of the application and changed the code significantly so it was more efficient, better designed, and easier to understand and maintain.\n\n##### Background\nPreviously, the Macintosh version of the application would only check the local network for a license violation on startup. As this was done on startup, it would lock up the application because the work was done on the main thread, and only licenses for the same platform were checked (licenses could be for Macintosh or Windows).\n\nI updated the application to perform the check on a secondary thread which could communicate problems to the main thread and capture the user's attention.\n\nIt would identify which computer and which user was using the license number with an option to send a message to the other user that they were in violation of the EULA.\n\nLater, using the improved cross-platform networking and threading libraries I made, I added the ability to periodically search the local network (rather than just on startup) for any copy of Vectoworks using the registered serial number license on either the Mac or Windows platform.\n\n"

