module.exports = "<!-- metadata\ntitle: test_center\ntags: Ruby, fastlane, xcodebuild, XCTest, XCUIText, Xcode, XML, Plist\n-->\n\n#### 🎯 The best fastlane plugin to understand and tame misbehaving iOS tests 🎉\n2018\n\n##### Why did I enjoy this project?\nI enjoyed this project because I set out with a vision of many useful tools to aid in testing iOS code, and I built it up in my spare time, each feature, each half hour adding more and more value to the plugin. Now, with many users asking for features and contributing to the codebase, I feel that I have contributed to world of developers using the Mac to produce software.\n\n##### Background\nAfter writing `setup_fragile_tests_for_rescan`, I had discovered new ways of controlling how tests are run. I also had developed other smaller plugins and I wasn't happy with how disorganized my code and fastlane plugins were.\n\nSo I decided to create a master plugin focused on testing that would provide many fastlane actions. With the focus on the eventual master action, `multi_scan`, I built up the foundational actions in an Agile way: delivering the highest value that I could as fast as I could.\n\nAs I was doing this project, my project, in my spare time, I could add the appropriate amount of tests, and choose what to work on. After almost a year of doing this, I now have a plugin that is ranked in the top twenty of fastlane available plugins, with 57 stars and many happy users.\n\n[test_center](https://github.com/lyndsey-ferguson/fastlane-plugin-test_center)"

