module.exports = "<!-- metadata\ntitle: Appian's Ansible MacOS playbooks\ntags: Jenkins, Ansible, Bash, S3, Xcode, Ruby, Python, Docker, launchctl, Homebrew, Anka, fastlane, Gradle, Mattermost\n-->\n\n#### Automated the configuration of Mac minis as Continuous Integration workers using Ansible\n2017-2018\n\n##### Why did I enjoy this project?\n\nI enjoyed this project because it provides confidence that our Mac mini computer clusters are set up in a way that is the same across each machine. Configuring the OS and adding, upgrading, or removing software is made as simple as running one command and the entire cluster matches the new configuration.\n\nAdditionally, all changes can be tracked in a Source Control Management system such as Git. That way, if a change that is made to the configuration breaks something, or doesn't work right, we can always go back to a previous version of a configuration.\n\n##### Background\n\nPreviously, setting up the Mac minis would be a manual process. Initially, it was manually done using a check list. Errors would often occur and any changes to the configuration would be hard to be made to each of the Mac minis.\n\nI converted the manual process into an automatic, correct, repeatable, and auditable process for configuring the Mac minis using Ansible, Bash, AWS S3, Homebrew, Docker, and other technologies.\n\nAfter working with existing playbook, I identified a need to modularize the playbook into distinct roles that could be run by using unique tags. I proposed this as the team's \"Quality Week\" project and it was chosen and completed to great joy.\n\nAfter completion of this project, a member of our highly esteemed infrastructure team, Release Engineering, stated that he wished his team's playbooks were as beautiful as ours were.\n\nFor more information, I suggest this article, [Ansible for Configuration Management](https://www.ansible.com/use-cases/configuration-management)"

