module.exports = "<!-- metadata\ntitle: Appian's Modern Web UI\ntags: React, JavaScript, Webpack, Java, Bash, REST, SAIL, GWT\n-->\n\n#### Build React front-end and JavaScript GUI components and engine code to replace GWT\n2016\n\n##### Why did I enjoy this project?\n I enjoyed this project because I worked on a new team that was the most Agile that I had ever worked with up to this point. While working towards the Product Owner's goals, this team decided on the priorities of the work tickets, we pair-programmed often, and when we didn't, we collaborated often. We \"swarmed\" on problem tickets when necessary. We tested each other's code, listened to each other's concerns and ideas: everything felt very efficient and effective.\n\nAlso, after working on the Mobile product for several years, I got a feel for what it was like to work on code that affects the majority of the Engineering department.\n\n##### Background\n Prior to 2017, Appian used [GWT](http://www.gwtproject.org) to power the front-end of their low-code application platform. Initially, this worked well because Software Engineers could write all their code in Java for both front-end web applications and back-end server.\n\nAfter some time, training people on GWT became expensive, debugging became more difficult, active development on the GWT toolset had slowed down, and Appian's web applications had not kept up with the times in terms of look and feel: existing and potential customers did not like the GUI.\n\nAppian decided it was time for a change and a major department-wide project was undertaken to overhaul the front-end web app to use [React](https://reactjs.org), a modern technology that has similar patterns as Appian's functional programming language, [SAIL](https://medium.com/appian-engineering/sail-appians-ui-framework-e0f2936f1d61), which is used to power customer's cross-platform applications.\n"

