module.exports = "<!-- metadata\ntitle: bluepill\ntags: XCTest, Objective-C\n-->\n\n#### Fixed issues and added features to the [bluepill](https://github.com/linkedin/bluepill) tool.\n2017\n\n##### Why did I enjoy this project?\nI enjoyed this project because I was able to significantly reduce the time that Appian's UI tests take, and I was able to contribute these same valuable improvements back to the Open Source community. I connected with people whom I normally wouldn't work with and learned how Apple's test infrastructure functions.\n\n##### Background\n\nAs part of an effort to improve iOS UI test time in Appian's Continuous Integration system, I installed LinkedIn's command line tool bluepill.\n\nBluepill takes a set of tests and runs them in parallel on multiple iOS Simulators in order to greatly reduce UI test time.\n\nThere were several improvements that I made to bluepill:\n- tests were all running on only one of the multiple simulators. I fixed that issue so that tests were divided as evenly as possible across the multiple simulators\n- the full test identifier had to be provided (`test-target/testsuite/testcase`). This made it difficult to use Xcode Schemes that configured testsuites to be skipped rather than explict testcases. I added a feature so that one can just provide the Test Suite to include or exclude all the tests in that suite.\n- I added the ability to parse xctestrun files to determine which tests should be included or excluded so that the project's Xcode Scheme was not necessary to determine that information."

