module.exports = "<!-- metadata\ntitle: Modern Mac OS X Vectorworks\ntags: Objective-C, Cocoa, Carbon, Xcode, C++, VectorScript\n-->\n\n#### Foretold, persuaded, planned, and led and executed the conversion of the Vectorworks CAD application to Objective-C. \n\n2008-2012\n\n##### Why did I enjoy this project?\nI enjoyed this project because I was a change agent leading the charge to save Vectorworks from stagnation. \n\nI saw the warning signs from the Apple Developer conferences: fewer and fewer resources were being invested in the Mac OS 9 compatible APIs (Carbon) and the proposed Carbon 64 API project appeared to be underfunded.\n\nAfter an analysis of the costs, benefits, and risks of choosing to convert Vectorworks to Carbon 64 vs. Objective-C, I convinced management to train a significant portion of the Engineering department and embark on a 4 year journey of converting parts of the application each yearly release cycle. \n\nAt one point, I told one of our third-party developers, it was like changing the engine of a car hurtling down the highway without stopping.\n\nAt the end of the project, I was happy that we had no major disruptions and the customers did not experience any problems. The only thing they did experience was a more modern UX as each part adopted the modern look and feel of the Mac OS.\n\n##### Background\n\nVectorworks began as MiniCAD in 1985 as a Mac only application writen in Pascal. In 1996, the QuickTime library was used as an emulation layer in order for a Windows version to be published. Some time in that past, the Pascal code was converted to C code using a third-party automation tool. Over time, more and more C++ code was used to develop the application.\n\nWhen Steve Jobs introduced Mac OS X, Vectorworks still used the Mac OS 9 APIs and would not launch natively on the new Operating System.\n\nVectorworks, like most other companies, continued doing the least it could do in terms of investing in infrastructure and modernizing the technology in order to deliver features to the customers to achieve growth targets.\n\nThat plan would have worked well if Apple had adopted the model that Windows at that time had adopted: maintain backwards compatibility at all costs. However, Steve Jobs wanted to reduce the complexity of supporting older software and effectively force software companies to either adopt, or perish.\n\nIt was a big shake up for Vectorworks, and it did affect the number of features that we could deliver, but in the end, the code base itself benefited because of the refactoring that we had to do and the time that we were given to make necessary improvements.\n\n\n\n"

