module.exports = "<!-- metadata\ntitle: Mac OS X Vectorworks (MachO)\ntags: Mac OS 9 APIs, Mach-O\n-->\n\n#### Converted the binary file format of the Vectorworks Mac application from OS 9 (CFM) to OS X (MachO).\n2001\n\n##### Why did I enjoy this project?\nI enjoyed this project, because it was an immense and important project, and I got to work with Apple Software Engineers directly to help improve Xcode and their C++ compilers.\n\n##### Background\n\nWhen Mac OS X was first introduced, Vectorworks required the Mac OS 9 emulator to run on the Operating System. This did not present an ideal experience to customers, and there were rumours that Apple would discontinue the service.\n\nI asked to take on the project of converting the Vectorworks Mac project from a Codewarrior Code Fragment Manager (CFM) binary format to a Xcode Mach-O binary format.\n\nAs an application with 10 millions lines of code including plugins, frameworks, libraries, cross-platform code, and front-end code, it was a daunting project. Vectorworks's sheer size presented unique challenges to Apple's compiler and linker teams when their jump codes used 32 bit integers which were not up to jumping the addresses that our large application needed. Apple needed to add special commands to switch to 64 bit integers for applications such as ours.\n\nThis project took me a year, one release cycle, and I was able to deliver the updated application without any customer issues. The only thing that they were aware of was that they no longer needed the OS 9 emulator for Vectorworks."

