module.exports = "<!-- metadata\ntitle: Vectorworks Architectural Setup Assistant\ntags: Vectorworks, VectorScript\n-->\n\n#### Improved the runtime of the automated architectural drawing setup tool from 8 mins to 80 seconds.\n1999\n\n##### Why did I enjoy this project?\nI enjoyed this project because I made the Setup Assistant re-entrant, simplified, and very very fast compared to what it used to be.\n\n##### Background\nWhen I first started working for Vectorworks Nemetschek, I joined as part of a small team, Content Engineering, where we focused on delivering speciality features for the different professionals who use Vectorworks: engineers, architects, set designers, etc.\n\nAfter consistently finishing projects faster than the team's manager could provide, and fixing all the bugs that I could, I found myself waiting around for work to do.\n\nCasting around, I saw a tool, the Vectorworks Setup Assistant, that was responsible for taking in the standards, and the goals of an architect, and setup the user's work environment; it was very slow. \n\nWith basic settings, it would take up to 8 minutes. I decided to do something about that.\n\nAfter putting together a workflow explaining how it worked, I could see that a lot of cycles were being wasted in repeated work that was wasting a lot of time. Each document layer was being revisited many times to perform many different operations. I simplified this flow so that work on an Vectorworks object was done in one place with the algorithm putting together the batch of work to be done.\n\nThe end result was that the whole setup would take 80 seconds instead of the 8 minutes that it used to. \n\nMore about the [Vectorworks Setup Assistant](https://www.architectsjournal.co.uk/home/vectorworks-architect/188841.article):\n\n> _The Architect's Setup Assistant helps take the hassle out of setting up layers, classes and sheets. With your input, it establishes a drawing organisation based on architectural drawing sheets. Each sheet is predefined with the correct layer and class organisation and pre-set Layer Links. Now, everything you draw on your plans is drawn correctly on your elevation and perspective sheets. The Setup Assistant also makes it easy to add new sheets, floors and a variety of standard architectural title blocks to help maintain house style across projects._\n"

