module.exports = "<!-- metadata\ntitle: XCUI IPC\ntags: Grand Central Dispatch, LLBSDMessaging, Objective-C, XCTest XCUI APIs \n-->\n\n#### XCUI Interprocess Communication framework for iOS\n2015\n\n##### Why did I enjoy this project?\nI enjoyed this project because I was able to separate the responsibilities of the iOS application, from the responsibilities of the test runner iOS application, to ensure that the iOS application was as close as possible to what is being sent to our customers.\n\n##### Background\nPreviously, Appian's iOS application was more tightly coupled with its environment. With the KIF test framework, the test runner portion of the application was basically the same as the actual application. This altered the application state and it reduced my confidence in the test results.\n\nWhen we converted our test framework from KIF to XCUI, I took on the task to improve the separation of responsibilities by creating a Interprocess Communication framework that allowed all the test data and \"network responses\" to come from the test runner application itself rather than an integrated part of a \"test\" version of the application.\n\nThis resulted in a version of the iOS application that is as close as possible to what is being shipped.\n"

